.sidebar{
    flex:1 1;
    height: calc(100vh - 50px);
    background-color: rgb(251, 251, 255);
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
}

.sidebarWrapper{
    padding: 20px;
    color: #555;
}

.sidebarMenu{
    margin-bottom: 10px;
}

.sidebarTitle{
    font-size: 13px;
    color: rgb(187, 186, 186);
}
.sidebarList{
    list-style: none;
    padding: 5px;
}
.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover{
    background-color: rgb(205, 205, 239);
}

.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}
.topbar{
    width: 100%;
    height: 50px;
    background-color: white;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    
}

.topbarwrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.logo{
    font-weight: bold;
    font-size: 30px;
    color: darkblue;
    cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;
}
.topbarIconsContainer{
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color: #555;

}

.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}
.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}
.container{
    display: flex;
    margin-top: 10px;
}

.link{
    text-decoration: none;
    color: inherit;
}

.chart{
    margin: 20px;
    padding: 20px;
    box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -webkit-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -moz-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
}
.chartTitle{
    margin-left: 12px;
}
.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;

}

.featuredItem{
    flex: 1 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
-webkit-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
-moz-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
}

.featuredTitle{
    font-size: 20px;
}

.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}
.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredIcon {
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featuredIcon.negative{
    color: red;
}
.featuredSub{
    font-size: 15px;
    color: gray;
}
.home{
    flex: 4 1;
}
.homeWidgets{
    display: flex;
    margin: 20px;
}
.widgetSm{
    flex: 1 1;
    box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -webkit-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -moz-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    padding: 20px;
    margin-right: 20px;
}
.widgetSmTitle{
    font-size: 20px;
    font-weight: 600;
}
.widgetSmImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.widgetSmList{
    margin: 0;
    padding: 0;
    list-style: none;
}
.widgetSmListItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}
.widgetSmUser{
    display: flex;
    flex-direction: column;
}
.widgetSmUsername{
    font-weight: 500;
}
.widgetSmUserTitle{
    font-weight: 250;
}
.widgetSmButton{
    display: flex;
    align-items: center;
    border: none;
    border: radius 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;

}
.widgetSmIcon{
    font-size: 16px !important;
    margin-right: 5px;
}
.widgetLg{
    flex: 2 1;
    box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -webkit-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    -moz-box-shadow: -1px -1px 0px -26px rgba(0,0,0,0.69);
    padding: 20px;
}

.widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
}

.widgetLgTable{
    width: 100%;
    border-spacing: 20px;

}
.widgetLgth{
    text-align: left;
}
.widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 500;
}

.widgetLgimg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.widgetLgDate,.widgetLgAmount{
    font-weight: 300;
}

.widgetLgButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}
.widgetLgButton.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}
.widgetLgButton.Declined{
    background-color: #fff0f1;
    color: #d95087;
}
.widgetLgButton.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;
}
.userList1{
    flex: 4 1;
}
.userListUser{
    display: flex;
    align-items: center;
}

.userListUserimg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.userListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}
.userListDelete{
    color: red;
    cursor: pointer;
}
